<template>
  <v-container class="bodyContent">
    <v-row class="text-left">
      <v-col cols="12" md="7">
        <div class="">
          <div class="text_title hidden-xs-only">
            TaWai for Health
            <div class="text_content mr-12 hidden-xs-only">
              Platform และกลไกเพื่อการจัดการความปลอดภัย
              ด้านผลิตภัณฑ์สุขภาพและการโฆษณา
              เพื่อการรายงานและแจ้งเตือนข้อมูลคุณภาพ
              <div align="center">
                <v-img
                  transition="scale-transition"
                  origin="center center"
                  src="http://www.tawainetwork.org/assetmaterials/regis.png"
                  class="hidden-xs-only"
                  contain
                  max-height="400"
                  max-width="450"
                />
              </div>
            </div>
          </div>
        </div>
      </v-col>

      <v-col cols="12" md="5">
        <div class="pa-4">
          <span class="text_question">ลงชื่อเข้าใช้งาน</span>
          <br />
          <span class="text_content">ใช้บัญชีตาไวของคุณ</span>
          <div class="pt-8">
            <v-text-field
              label="อีเมลผู้ใช้งาน"
              outlined
              rounded
              v-model="userAuth.email"
              :error-messages="emailErrors"
              required
              @input="$v.userAuth.email.$touch()"
              @blur="$v.userAuth.email.$touch()"
            ></v-text-field>
            <v-text-field
              label="รหัสผ่าน"
              outlined
              rounded
              :append-icon="hidePWD ? 'mdi-eye' : 'mdi-eye-off'"
              :type="hidePWD ? 'text' : 'password'"
              @click:append="hidePWD = !hidePWD"
              v-model="userAuth.pwd"
              :error-messages="pwdErrors"
              required
              @input="$v.userAuth.email.$touch()"
              @blur="$v.userAuth.pwd.$touch()"
            ></v-text-field>
            <v-btn
              class="my-6"
              depressed
              block
              rounded
              x-large
              color="#00B5F8"
              dark
              @click="click_login"
            >
              เข้าสู่ระบบ</v-btn
            >
            <v-btn
              :ripple="false"
              :retain-focus-on-click="false"
              text
              block
              rounded
              large
              color="#00B5F8"
              @click="click_forgetPwd"
              >ลืมรหัสผ่านหรือไม่</v-btn
            >
            <v-row>
              <v-col align="center">
                <span class="text_content">หากคุณยังไม่ได้เป็นสมาชิก </span>
                <router-link to="/register">สมัครเลย</router-link>
              </v-col>
            </v-row>
          </div>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, email } from "vuelidate/lib/validators";
import EventBus from "./EventBus";
export default {
  mixins: [validationMixin],
  validations: {
    userAuth: {
      email: { required, email },
      pwd: { required, minLength: minLength(6) }
    }
  },
  data: () => ({
    hidePWD: false,
    getAccessStatus: true,
    userAuth: {
      email: "",
      pwd: ""
    }
  }),
  computed: {
    emailErrors() {
      const errors = [];
      if (!this.$v.userAuth.email.$dirty) return errors;
      !this.$v.userAuth.email.email && errors.push("รูปแบบของอีเมลไม่ถูกต้อง");
      !this.$v.userAuth.email.required &&
        errors.push("กรุณาระบุอีเมลผู้ใช้งาน");
      if (!this.getAccessStatus) errors.push("ตรวจสอบอีเมลอีกครั้ง");
      return errors;
    },
    pwdErrors() {
      const errors = [];
      if (!this.$v.userAuth.pwd.$dirty) return errors;
      !this.$v.userAuth.pwd.minLength &&
        errors.push("กรุณาระบุรหัสผ่านอย่างน้อย 6 ตัวอักษร");
      !this.$v.userAuth.pwd.required && errors.push("กรุณาระบุรหัสผ่าน");
      if (!this.getAccessStatus) errors.push("ตรวจสอบรหัสผ่านอีกครั้ง");
      return errors;
    }
  },
  methods: {
    async click_login() {
      this.$v.$touch();
      if (this.userAuth.email.length > 0 && this.userAuth.pwd.length > 0) {
        await fetch("http://www.tawainetwork.org/webBackendPHP/login.php", {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userEmail: this.userAuth.email,
            userPWD: this.userAuth.pwd
          })
        })
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "success") {
              localStorage.setItem("userToken", dataRes[1]["TokenSession"]);
              localStorage.setItem("userID", dataRes[1]["UserId"]);
              localStorage.setItem("userRole", dataRes[1]["Role"]);
              const userTypeAccess = {
                status: "logged_in",
                role: dataRes[1]["Role"],
                ID: dataRes[1]["UserId"],
                Token: dataRes[1]["TokenSession"]
              };
              EventBus.$emit("userACCESS", userTypeAccess);
              this.$router.push("mainuser");
            } else {
              this.getAccessStatus = false;
            }
          })
          .catch(err => {
            console.log("ERR.loginRequest: ", err);
          });
      }
    },
    async checkUserAuth_local() {
      let checkAuth = localStorage.getItem("userToken");
      if (checkAuth) {
        let userID = localStorage.getItem("userID");
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/recheckSession.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: userID,
              userToken: checkAuth
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "failed") {
              localStorage.removeItem("userToken");
              localStorage.removeItem("userID");
              localStorage.removeItem("userRole");
              this.$router.push("login");
            } else {
              this.$router.push("mainuser");
            }
          })
          .catch(err => {
            console.log("ERR.requestAccess.Session_login: ", err);
          });
      } else {
        this.$router.push("login");
      }
    },
    click_forgetPwd() {
      //
    }
  },
  mounted() {
    this.checkUserAuth_local();
  }
};
</script>

<style scoped>
.bodyContent {
  width: 100%;
  max-width: 1264px;
  padding: 20px;
  padding-bottom: 60px;
  padding-top: 70px;
}
.text_title {
  font-size: 28px;
  color: #424242;
}
.text_content {
  font-size: 16px;
  color: #424242;
}
.text_question {
  font-size: 34px;
  font-weight: 500;
  color: #424242;
}
</style>
